<template>
  <div class="wrapper">
    <div class="container">
      <img :src="picture.src" class="picture" />
    </div>
    <p>
      <span class="time">
        {{ picture.time }}
      </span>
      <span class="title">
        {{ picture.title }}
      </span>
    </p>
  </div>
</template>

<script>
export default {
  name: "",
  components: {},
  props: [],
  data() {
    return {
      pictureArray: [],
      picture: {},
    };
  },
  computed: {},
  watch: {},
  async created() {
    let { data } = await this.$get("picture.json");
    this.pictureArray = data.picture;
    console.log(data);
    if (this.pictureArray.length > 0) {
      let i = Math.ceil(Math.random() * this.pictureArray.length - 1);
      this.picture = this.pictureArray[i];
    }
  },
  mounted() {},
  methods: {},
};
</script>

